.about-us {
  background-image: url('../img/bg2.png');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  padding-top: 40px;
  padding-bottom: 36px;
  .main-phone {
    padding: 15px;
   
    .phone-about {
      padding-top: 50px;
      img {
        margin: auto;
      }
    }
    h2 {
      font-size: 35px;
      font-family: 'Montserrat', sans-serif;
      color: rgb(51, 51, 51);
      text-align: center;
      font-weight: bold;
      margin-bottom: 0;
    padding-bottom: 14px;
    }
    .number {
      font-size: 22px;
      font-family: 'Montserrat', sans-serif;
      color: #333;
      line-height: 1.2;
      text-align: center;
      font-weight: bold;
      margin-bottom: 0;
      padding: 10px 0;
      
      span {
        font-size: 22px;
        font-family: 'Montserrat', sans-serif;
        color: rgb(175, 196, 201);
        line-height: 1.2;
        text-align: center;
      }
    }
    .phone-info {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
     
      margin-bottom: 0;
      padding-bottom: 10px;
      span{
        font-family: 'Montserrat', sans-serif;
        font-size: 15px;
        color: rgb(51, 51, 51); 
        padding: 0 10px;
      }
      &::before {
        content: " ";
        display: block;
        background-color: rgb(175, 196, 201);
        width: 151px;
        height: 1px;
        align-self: center;
        .sm-block({
          width: 120px;
        });
      }
      &::after {
        content: " ";
        display: block;
        background-color: rgb(175, 196, 201);
        width: 151px;
        height: 1px;
        align-self: center;
        .sm-block({
          width: 120px;
        });
      }
    }
    .about-text {
      font-size: 14px;
      font-family: 'Montserrat', sans-serif;
      color: rgb(51, 51, 51);
      text-align: center;
      margin-bottom: 0;
    }
    form {
      text-align: center;
     
      label {
        display: block;
        font-size: 16px;
        font-family: 'Montserrat', sans-serif;
        color: rgb(175, 196, 201);
        
      }
      input {
        border-style: solid;
        border-width: 1px;
        border-color: rgb(175, 196, 201);
        border-radius: 10px;
        width: 354px;
        height: 54px;
        padding: 20px;
        margin-top: 20px;
        .sm-block({
          width: 260px;
        });
      }
    }
    .call{
        padding-top:30px;
        width: 100%;
        text-align: center;
        a{
                margin: auto;
                font-size: 14px;
                font-family: "Montserrat";
                color: rgb(255, 255, 255);
                font-weight: bold;
                text-align: center; 
                background: #1db1a8;
                border: 1px solid #1db1a8;
                width: 260px;
                height: 50px; 
                display: block;
                line-height: 50px;
                text-decoration: none;
                text-transform: uppercase;
                border-radius: 30px;
                svg{
                  margin-left: 5px;
                }
                   
        }
    }
  }
}