header {
  .sina-nav {  
    .extension-nav {
      float: right;
      display: flex;
      align-items: center;
      ul {
        li {
          a {
            .shop-badge {
              background: #6fbe62;
            }
          }
        }
      }
      .form {
        .nice-select {
          height: 24px;
          line-height: 18px;
          border-radius: 7px;
          span {
            font-size: 12px;
           text-transform: capitalize;
          }
        }
      }
      .item-top {
        .lg-block( {
          display: none;
        }
        );
        margin-left: 35px;
        text-align: right;
        p {
          font-size: 14px;
          color: #afc4c9;
          margin: 0;
          line-height: 17px;
          i {
            padding-right: 12px;
          }
          span {
            font-size: 22px;
            color: #333333;
            font-weight: bold;
            .xl-block({
              font-size: 20px;
            });
          }
          a {
            font-size: 13px;
            font-family: 'Montserrat', sans-serif;
            color: rgb(25, 118, 210);
            line-height: 1.2;
          }
        }
      }
    }
    #navbar-menu {
      .sina-menu {
        .active {
          border-bottom: 5px solid #1085e4;
        }
        li {
          a{
            .xl-block({
              font-size: 12px;
              padding: 20px 5px;
            });
          }
          .dropdown-toggle {
            &::before {
              display: none;
             
            }
            &::after {
              display: none;
            }
          }
          #drop-top {
            top: 120%;
            background: #fff;
            &::after {
              content: " ";
              position: absolute;
              top: -8px;
              left: 50%;
              transform: rotate(45deg);
              width: 15px;
              height: 15px;
              background: #fff;
            }
            li {
              a {
                &:hover {
                  background-color: #fff;
                  color: #1085e4;
                }
                i {
                  margin-left: 5px;
                }
              }
            }
          }
        }
      }
    }
    
    
    .sina-nav-header {
        .sina-brand {
          .sm-block({
            padding: 8px;
           margin: 0;
           float: left;
          });
          .md-block({
          padding: 6px;
           float: none;
          });
        img{
         
        }
      }  
    }
  }
 
}
