.portfolio-web {
  background-image: url('../img/bg copy 2.png');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 50px 0;
 
  .text-web {
   
    font-family: 'Montserrat', sans-serif;
    h2 {
      font-size: 35px;
      color: rgb(51, 51, 51);
      font-weight: bold;
      text-align: center;
      padding-bottom: 20px;
      margin-bottom: 0;
      .sm-block({
        font-size: 24px;
      });
    }
    p {
      font-size: 14px;
      color: rgb(51, 51, 51);
      margin: 0;
     
      text-align: center;
      .sm-block({
        font-size: 10px;
      });
    }
  }

  
   .menu-web {
    margin: 25px auto;
    ul {
      border: 1px solid #fff;
      border-radius: 30px;
      background: #fff;
      padding: 3px;
      justify-content: space-between;
      .sm-block({
        width: 150px;
        margin: auto;
        display: block;
      });
      .lg-block({
        width: 75%;
        margin: auto;
      
      });
     
      .nav-item {
        
        &:hover {
          background: #4db97f;
          color: #fff;
          border-radius: 30px;
        }
        .active {
          background: #4db97f;
          color: #fff;
          border-radius: 30px;
        }
        .nav-link {
          border: none;
          font-size: 16px;
          font-family: 'Montserrat', sans-serif;
          color: #e6ecee;
          padding: 8px 20px;
          outline: none;
          .xl-block({
            font-size: 14px;
           padding: 8px 16px;
          });
          .md-block({
            font-size: 10px;
          });
          .sm-block({
            font-size: 12px;
          });
        }
      }
    }
  }
 
 
  .tab-content {
    .tab-pane {
      .port-item{
        padding: 15px;
        .item-web {
          img{
            width: 100%;
            
          }
          p {
            background: #cadce6;
            margin-bottom: 0;
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
            height: 16px;
            .svg-inline--fa {
              position: absolute;
              right: 10%;
              color: #fff;
              font-size: large;
              cursor: pointer;
            }
          }
        }
        .text-dizayn {
        
          border-bottom-left-radius: 10px;
          border-bottom-right-radius: 10px;
          background: #fff;
          text-align: left;
          padding: 20px 16px 16px 16px;
          h4 {
            font-size: 16px;
            font-family: 'Montserrat', sans-serif;
            color: #1976d2;
            font-weight: bold;
            line-height: 10px;
            margin-bottom: 0;
            .sm-block({
              font-size: 14px;
            });
          }
          .heart{
            position: absolute;
            right: 10%;
           font-size: x-large;
           bottom: 22%;
           cursor: pointer;
         color: #bbb;
        
          }
          p {
            margin-bottom: 0;
            padding-top: 10px;
            padding-bottom: 26px;
              font-size: 14px;
              font-family: 'Montserrat',sans-serif;
              color: rgb(51, 51, 51);
              .sm-block({
                font-size: 10px;
              });
          }
          .price{
            width: 100%;
            display: flex;
            justify-content: space-between;
            .star {
              font-size: 14px;
         
              .sm-block({
                font-size: 12px;
              });
             
            }
            .sent {
                font-size: 16px;
                font-family: 'Montserrat',sans-serif;
                color: rgb(51, 51, 51);
                font-weight: bold;
                .sm-block({
                  font-size: 12px;
                });
  
               
            }
          }
        
        }
      }
     
    }
  }
  .port-button{
   text-align: center;
   
    a{
      margin-top: 25px;
        background-color: rgb(255, 255, 255);
      width: 236px;
         height: 50px; 
          font-size: 14px;
          font-family: 'Montserrat',sans-serif;
          color: rgb(153, 153, 153);
          font-weight: bold;
          text-align: center;
        line-height: 50px;
        display: block;
        border-radius: 30px;
        text-decoration: none;
        
        .sm-block({
          font-size: 12px;
        });
      svg{
        margin-left: 5px;
      }
      
    }
  }
}