@import "./params/mix.less";
@import "./params/vars.less";
@import "./header.less";
@import "./main-slider.less";
@import "./main-services.less";
@import "./portfolio-web.less";
@import "./development.less";
@import "./main-pay.less";
@import "./about-us.less";
@import "./footer.less";
img {
  max-width: 100%;
  height: auto;
}

body {
  min-width: 320px;
}

.red {
  svg {
    color: #8bc34a;
  }
}

.path {
  svg {
    color: #8bc34a;
  }
  .star2 {
    svg {
      &:nth-child(1),
      &:nth-child(2) {
        color: #8bc34a;
      }
    }
  }
  .star3 {
    svg {
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3) {
        color: #8bc34a;
      }
    }
  }
  .star4 {
    svg {
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4) {
        color: #8bc34a;
      }
    }
  }
  .star5 {
    svg {
      color: #8bc34a;
    }
  }
}