.main-services {
 padding: 50px 0;
 display: flex;
 flex-wrap: wrap;
 justify-content: center;
 margin: 0 15px;
  h2 {
    padding-bottom: 50px;
    font-size: 35px;
    font-family: 'Myriad Pro', sans-serif;
    color: rgb(51, 51, 51);
    text-align: center;
    margin-bottom: 0;
    .sm-block({
      font-size: 25px;
    });
  }
  .web-top {
   padding:15px;
    text-align: center;
    .body-to {
        height: 70px;
        overflow: hidden;
    }
    .text-to {
      padding-top: 16px;
      text-align: center;
     
      h5 {
        font-weight: bold;
        margin: 0;
        font-size: 16px;
        color: #333333;
        font-family: 'Myriad Pro', sans-serif;
      }
        p {
        
        padding-top: 15px;
        margin-bottom: 0;
          font-size: 14px;
          color: #414141;
          font-family: 'Myriad Pro', sans-serif;
        }
    
    }
  }
  .main-web{
 padding-top: 35px;
  text-align: center;
 width: 100%;
  a{
  
      margin: auto;
      border-style: solid;
      border-width: 1px;
      border-color: rgb(175, 196, 201);
      width: 204px;
      height: 54px;
        font-size: 14px;
        font-family: 'Montserrat',sans-serif;
        color: rgb(175, 196, 201);
        font-weight: bold;
        text-align: center;
        display: block;
        line-height: 50px;
        text-decoration: none;
        border-radius: 30px;
        svg{
          margin-left: 5px;
        }
      
  }
  }
}