.footer {
  justify-content: space-around;
  background: #0c181a;
  padding: 25px 0;
  .foter-top {
    padding: 15px;
    .logotip{
      .sm-block({
        text-align: center;
      });
    }
    .menu-footer {
      display: flex;
      flex-wrap: wrap;
      .item {
        margin: auto;;
        .lg-block({
          display: none;
        });
        ul {
          list-style: none;
          padding: 0;
          li {
            text-align: left;
            a {
              font-size: 15px;
              font-family: 'Montserrat', sans-serif;
              color: rgb(175, 196, 201);
              text-decoration: none;
              &:hover{
                color:  #8bc34a;
                 }

            }
          }
        }
      }
      .item1 {
        .lg-block({
        margin: auto;
        });
        p {
          font-size: 15px;
          font-family: 'Montserrat', sans-serif;
          color: rgb(175, 196, 201);
          text-align: center;
          margin-bottom: 0;
        }
        ul {
          list-style: none;
          display: flex;
          justify-content: center;
          padding: 0;
          li {
          
            margin:6px;


          }
                    a {  background: #afc4c9;
            text-decoration: none;
            color: #0c181a;
            border: 1px solid  #8bc34a;
            border-radius: 50%;
            width: 24px;
            height: 24px;
            text-align: center;
            display: block;
            
            &:hover{
              background:  #8bc34a;
              color: #fff;
               }
            }
        }
      }
    }
    .footer-item{
      .sm-block({
        text-align: center;
      });
        p{
            margin-bottom: 0;
                font-size: 14px;
                font-family: 'FontAwesome5ProSolid',sans-serif;
                color: rgb(175, 196, 201);
             
               span{
                color: #8bc34a;
                   font-weight: bold;
                   font-size: 14px;
               }
               a{
              
                    font-size: 14px;
                    font-family: 'FontAwesome5ProSolid',sans-serif;
                    color: #8bc34a;
                    text-decoration: none;
                    
               .svg-inline--fa {
                    color:#afc4c9;  
                }
                 
               }
              
              
        }
    }
  }
}