.main-pay {
  background-image: url('../img/bg1.png');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  padding: 60px 0;
  h2 {
    font-size: 35px;
    font-family: 'Montserrat', sans-serif;
    color: rgb(51, 51, 51);
    font-weight: bold;
    text-align: center;
    padding-bottom: 55px;
    margin-bottom: 0;
    .sm-block({
      font-size: 25px;
    });
  }
  .item-payme {
    padding: 15px;
    &:nth-child(2) {
      .angle{
        .lg-block({
           display: none;
        });
          
      }
   
    }
      .angle{
        position: absolute;
        top: 85px;
        right: -3px;
        color: #afc4c9;
      
        &::after{
          content: "";
          border: 1px dashed #afc4c9;
          width: 100px;
          height:0;
          position: absolute;
          top: 12px;
          left: 10px;
        
         
          .sm-block( {
          display: none;
         });
        }
        &::before{
          content: "";
          border: 1px dashed #afc4c9;
          width: 100px;
          height:0;
          position: absolute;
          top: 12px;
          right: 10px;
         
          .sm-block( {
           display: none;
           });
        }
      }
  
     
      }
    .web-delevopment {
      position: relative;
      background-color: rgb(255, 255, 255);
      width: 161px;
      height: 161px;
      border-radius: 50%;
      text-align: center;
      margin: auto;
      display: flex;
      align-items: center;
      justify-content: center;
     z-index: 99;
      .main-two {
        font-size: 16px;
        background: #afc4c9;
        color: #fff;
        position: absolute;
        height: 20px;
        width: 20px;
        top: 5%;
        right: 8%;
        border-radius: 50%;
        display: block;
        line-height: 20px;
      }
    }
    .text-item {
      padding-top: 36px;
      text-align: center;
      h4 {
        font-size: 16px;
        font-family: 'Montserrat', sans-serif;
        color: rgb(51, 51, 51);
        font-weight: bold;
        text-align: center;
        margin-bottom: 0;
        .sm-block({
          font-size: 14px;
        });
      }
      p {
        font-size: 14px;
        font-family: 'Montserrat', sans-serif;
        color: #333333;
      
        text-align: center;
        margin-bottom: 0;
        padding-top: 12px;
        padding-bottom: 5px;
        .sm-block({
          font-size: 12px;
        });
      }
    }
  }
 
  .payme-top {
    display: flex;
    flex-wrap: wrap;
    width: 270px;
    justify-content: center;
    .lg-block({
      margin: auto;
    });
    a {
      border: 1px solid #fff;
      background: #fff;
      text-align: center;
      height: 40px;
      width: 70px;
      border-radius: 10%;
      line-height: 40px;
      display: block;
      margin: 5px;
    }
    .payme {}
  }
