.main-slider {
  background: url('../img/bg.png') center no-repeat;
  background-size: cover;

  .s-item {
    .slider-item {
      display: flex;
      width: 100%;
      justify-content: space-between;
     .xl-block({
       flex-direction: column;
       justify-content: center;
     });
      .slider-text {
        position: relative;
        padding-top: 130px;
        width: 40%;
        .xl-block({
          width: 100%;
          text-align: center;
        });
        .md-block({
          padding-top: 70px;
        });
        h2 {
          .xl-block( {
            br {
              display: none;
            }
          });
          .md-block({
            padding-bottom: 20px;
          });
          .sm-block({
           font-size: 33px;
          });
          font-size: 40px;
          font-family: 'Montserrat',
          sans-serif;
          color: rgb(51, 51, 51);
          font-weight: bold;
          padding-bottom: 70px;
          margin: 0;
        }

        .sum {
          font-size: 40px;
          font-family: 'Montserrat',sans-serif;
          color: rgb(255, 255, 255);
          font-weight: bold;
          text-align: center;
          position: absolute;
          max-width: 211px;
          max-height: 211px;
          background: #1381d7;
          border-radius: 50%;
          padding: 70px 20px;
          line-height: 30px;
          top: 70px;
          right: -75px;
          z-index: 1;
          .xl-block( {
            right: 70%;
            top: 300px;
          });
          .lg-block( {
          left: 0;
          top: 290px;
          });

          .md-block( {     
            padding: 45px 0;
            font-size: 25px;
            top: 200px;
          });
          .sm-block({
            padding: 7px 0;
            font-size: 18px;
            border-radius: 9px;
            display: flex;
            top: 100%; 
            width: 200px;
            justify-content: center;
            left: 0;
            right: 0;
            margin: auto;
          });
          .valute {
            font-size: 25px;
          }

        }

        .ord {
          a {


            font-size: 14px;
            font-family: 'Montserrat', sans-serif;
            color: rgb(255, 255, 255);
            font-weight: bold;
            text-align: center;
            background-image: -moz-linear-gradient(0deg, rgb(139, 195, 74) 0%, rgb(0, 172, 193) 100%);
            background-image: -webkit-linear-gradient(0deg, rgb(139, 195, 74) 0%, rgb(0, 172, 193) 100%);
            background-image: -ms-linear-gradient(0deg, rgb(139, 195, 74) 0%, rgb(0, 172, 193) 100%);
            width: 200px;
            height: 50px;
            border-radius: 30px;
            line-height: 50px;
            display: block;
            text-decoration: none;

            .xl-block( {
              margin: auto;
              });

            svg {
              margin-left: 5px;
            }
          }
        }
      }

      .slider-photo {
        width: 60%;  
        position: relative;
        padding-top: 70px;
        .xl-block({
          width: 100%;
        });
        .phone {
          position: absolute;
          z-index: 1;
          top: 6%;
          right: 12%;

          .xl-block( {
           right: 22%;
          });

          .lg-block( {
          right: 17%;
          top: 12px;
          });
          .sm-block({
            width: 40%;
            right: 14%;
            top: 14%;
            
          })
        }

        .watch {
          position: absolute;
          z-index: 1;
          top: 35%;
          right: 0%;
        
          .xl-block( {
            right: 12%;
          });
          .lg-block( {
            right: 0%;
            top: 39%;
          });
          .md-block( {
            right: -1%;
            top:40%;
          });
          .sm-block( {
            right: -1%;
            top:50%;
            width: 20%;
          });
        }

        .macbook {
          margin: 70px 0 0 0;
          position: relative;
          z-index: 3;
          right: 0;
          .xl-block( {
            margin: auto;
            margin-top: 70px;
          });
          .md-block({
           margin-top: 0;
          });
          .sm-block({
            padding-top: 40px;
          });
        }

      }

    }

    .slick-dots {
      bottom: 15px;

      li {
        &.slick-active {
          button {

            background-color: rgb(255, 255, 255);


          }
        }

        button {
          width: 12px;
          height: 12px;
          border-style: solid;
          border-width: 2px;
          border-color: rgb(255, 255, 255);
          border-radius: 50%;
          background-color: rgba(255, 255, 255, 0);

          &::after {
            display: none;
          }

          &::before {
            display: none;
          }
        }
      }
    }
  }


}