.development {
  padding: 50px;
 
  .the-site {
    .xl-block({
      text-align: center;
    });
    .site-title{
        
      h2 {
        font-size: 35px;
        font-family: 'Montserrat', sans-serif;
        color: rgb(51, 51, 51);
        font-weight: bold;
     
        margin-bottom: 0;
  
        .sm-block( {
          font-size: 25px;
        }
  
        );
      }
  
      .site-text {
        font-size: 14px;
        font-family: 'Montserrat', sans-serif;
        color: rgb(51, 51, 51);
     
        margin-bottom: 0;
        padding-top: 20px;
      
      
        .sm-block( {
          font-size: 12px;
        }
  
        );
      }
    }
  

    .develop-top {
      display: flex;
      flex-wrap: wrap;
      padding: 15px;
      margin-top: 25px;
      justify-content: center;

      .develop {
        text-align: left;
      }

      .text-site {
        flex-wrap: wrap;
        p {
          font-size: 14px;
          font-family: 'Montserrat', sans-serif;
          color: rgb(51, 51, 51);
          margin: 0;
          text-align: left;
          padding-left: 20px;

          .lg-block( {
            font-size: 14px;
          });
          .md-block( {
            font-size: 14px;
            padding-top: 10px;
          });

          .sm-block( {
            font-size: 12px;
          }

          );
        }
      }
    }

    .button {
      padding: 35px 0;
      width: 100%;
     
      .xl-block({
        text-align: center;
      });
   
      a {
        font-size: 14px;
        font-family: 'Montserrat', sans-serif;
        color: rgb(175, 196, 201);
        font-weight: bold;
        text-align: center;
        border-style: solid;
        border-width: 1px;
        border-color: rgb(175, 196, 201);
        width: 260px;
        height: 54px;
        display: inline-block;
        line-height: 50px;
        text-transform: uppercase;
        border-radius: 30px;
        text-decoration: none;
       
        .sm-block( {
          font-size: 12px;
          width: 180px;

        }

        );

        svg {
          margin-left: 5px;
        }
      }
    }
  }

  .collection {
    position: relative;
    padding-bottom: 50px;

    .headphone {
      position: absolute;
      z-index: -2;
      right: 60%;
      top: 6%;
      .lg-block({
        top: 0%;
        right: 66%;
      });

      .md-block( {
         top: 2%;
         right: 58%;;   
      });
      .sm-block( {
        top: 2%;
        right: 58%;
        width: 164px;
     });
      
    }

    .shadow1 {}

    .nicon {
      position: absolute;
      z-index: -2;
      right: 15%;
      top: 0;
      .xl-block( {
        top: -3%;
        right: 4%;
      });

      .lg-block( {
        top: 0%;
        right: 32%;
      });
      .md-block({
        top: 0%;
        right: 8%;;
      });
      .sm-block({
        top: 0%;
        right: 0%;
        width: 110px;;
      });
    }

    .graviti1 {
      position: absolute;
      z-index: 1;
      left: -19%;
      top: 1%;

      .xl-block( {
        left: -22%;
        top: -2%;
      }

      );
      .lg-block( {
     top: 2%;
     left: -14%;
      });

      .md-block( {
     top: 10%;
     left: -10%;
      }

      );
    }

    .graviti {
      position: absolute;
      z-index: 2;
      right: 17%;
      top: 10%;

      .xl-block( {
        top: 4%;
        right: 7%;
            });

      .lg-block( {
        top: 10%;
        right: 34%;
      });
      .md-block({
        top: 7%;
        right: 25%;
      });
    }

    .pen {
      position: absolute;
      right: 68%;
      top: 68%;
      .lg-block({
        top: 73%;
        right: 73%;
      });
      .md-block({
        top: 70%;
        right: 67%;
      });
      .sm-block({
        top: 70%;
        right: 67%;
        width: 160px;
      
      });
    }
  }
}
